import Layout from '../components/Layout'
import Header from '../components/Header'
import Content from '../components/Content'

const Index = () => (
  <Layout>
    <Header />
    <Content />
      <style jsx global>{`
        body {
          margin: 0;
          font-size: 14px;
          font-weight: 200;
          color: #4a4a4a;
          background-color: #f7f7f7;
        }
        .wrapper {
          width: 100%;
          margin: 0;
          padding: 0;
          display: flex;
          justify-content: center;
        }
        .container {
          width: 100%;
          max-width: 480px;
          display: flex;
        }
        h1 {
          font-size: 16px;
        }
      `}</style>
  </Layout>
);

export default Index;